// =============================================================================
// GET /api/metrics/webhooks - Webhook Delivery Metrics
// =============================================================================
// Returns webhook delivery statistics

import { NextRequest, NextResponse } from 'next/server';
import { getWebhookMetrics, WEBHOOK_CONFIG } from '@/lib/webhook-delivery';
import { requireAuth } from '@/lib/auth';

export async function GET(request: NextRequest) {
  // Require auth to view metrics
  const auth = await requireAuth(request, ['admin', 'user']);
  if (!auth.authorized) {
    return NextResponse.json(
      { error: 'Unauthorized', code: 'UNAUTHORIZED' },
      { status: 401 }
    );
  }
  
  const metrics = getWebhookMetrics();
  
  return NextResponse.json({
    success: true,
    data: {
      metrics,
      config: {
        maxAttempts: WEBHOOK_CONFIG.maxAttempts,
        baseDelayMs: WEBHOOK_CONFIG.baseDelay,
        maxDelayMs: WEBHOOK_CONFIG.maxDelay,
        timeoutMs: WEBHOOK_CONFIG.requestTimeout,
        dlqEnabled: WEBHOOK_CONFIG.enableDLQ,
        jitterFactor: WEBHOOK_CONFIG.jitterFactor,
      },
    },
    timestamp: new Date().toISOString(),
  });
}

