// =============================================================================
// Webhook Delivery System
// =============================================================================
// Handles webhook event delivery with retry logic, HMAC signing, and status tracking
//
// Features:
// - Exponential backoff with jitter
// - Dead letter queue (DLQ) for failed webhooks
// - Delivery metrics tracking
// - Circuit breaker per endpoint
// - Structured logging

import crypto from 'crypto';
import { createLogger } from './logger';
import * as webhookDeliveriesDb from '@/db/webhook-deliveries';

// Lazy import to avoid blocking if bullmq is not available
async function getWebhookQueue() {
  try {
    const { enqueueWebhookDelivery } = await import('./webhook-queue');
    return enqueueWebhookDelivery;
  } catch (error) {
    logger.warn('Webhook queue not available (bullmq may not be installed)', { error });
    return null;
  }
}

const logger = createLogger({ component: 'WebhookDelivery' });

// =============================================================================
// CONFIGURATION
// =============================================================================

export const WEBHOOK_CONFIG = {
  /** Maximum number of delivery attempts */
  maxAttempts: parseInt(process.env.WEBHOOK_MAX_ATTEMPTS || '5', 10),
  
  /** Base delay for exponential backoff (ms) */
  baseDelay: parseInt(process.env.WEBHOOK_BASE_DELAY_MS || '1000', 10),
  
  /** Maximum delay between retries (ms) */
  maxDelay: parseInt(process.env.WEBHOOK_MAX_DELAY_MS || '300000', 10), // 5 minutes
  
  /** Request timeout (ms) */
  requestTimeout: parseInt(process.env.WEBHOOK_TIMEOUT_MS || '10000', 10),
  
  /** Enable dead letter queue for failed webhooks */
  enableDLQ: process.env.WEBHOOK_ENABLE_DLQ !== 'false',
  
  /** Jitter factor (0-1) for randomizing delays */
  jitterFactor: parseFloat(process.env.WEBHOOK_JITTER_FACTOR || '0.2'),
};

// =============================================================================
// METRICS
// =============================================================================

interface WebhookMetrics {
  delivered: number;
  failed: number;
  retried: number;
  dlqCount: number;
  byEndpoint: Map<string, { delivered: number; failed: number }>;
  lastReset: number;
}

const metrics: WebhookMetrics = {
  delivered: 0,
  failed: 0,
  retried: 0,
  dlqCount: 0,
  byEndpoint: new Map(),
  lastReset: Date.now(),
};

const METRICS_RESET_INTERVAL = 60 * 60 * 1000; // 1 hour

function recordDeliveryMetric(url: string, success: boolean, isRetry: boolean = false): void {
  // Reset metrics hourly
  if (Date.now() - metrics.lastReset > METRICS_RESET_INTERVAL) {
    metrics.delivered = 0;
    metrics.failed = 0;
    metrics.retried = 0;
    metrics.byEndpoint.clear();
    metrics.lastReset = Date.now();
  }
  
  if (success) {
    metrics.delivered++;
  } else {
    metrics.failed++;
  }
  
  if (isRetry) {
    metrics.retried++;
  }
  
  // Track by endpoint (use hostname only)
  try {
    const hostname = new URL(url).hostname;
    const endpointMetrics = metrics.byEndpoint.get(hostname) || { delivered: 0, failed: 0 };
    if (success) {
      endpointMetrics.delivered++;
    } else {
      endpointMetrics.failed++;
    }
    metrics.byEndpoint.set(hostname, endpointMetrics);
  } catch {
    // Invalid URL, skip endpoint tracking
  }
}

/**
 * Get webhook delivery metrics
 */
export function getWebhookMetrics(): {
  delivered: number;
  failed: number;
  retried: number;
  dlqCount: number;
  successRate: string;
  byEndpoint: Record<string, { delivered: number; failed: number; successRate: string }>;
  lastReset: string;
} {
  const total = metrics.delivered + metrics.failed;
  const byEndpoint: Record<string, any> = {};
  
  metrics.byEndpoint.forEach((stats, hostname) => {
    const endpointTotal = stats.delivered + stats.failed;
    byEndpoint[hostname] = {
      ...stats,
      successRate: endpointTotal > 0 
        ? ((stats.delivered / endpointTotal) * 100).toFixed(2) + '%'
        : '0%',
    };
  });
  
  return {
    delivered: metrics.delivered,
    failed: metrics.failed,
    retried: metrics.retried,
    dlqCount: metrics.dlqCount,
    successRate: total > 0 
      ? ((metrics.delivered / total) * 100).toFixed(2) + '%'
      : '0%',
    byEndpoint,
    lastReset: new Date(metrics.lastReset).toISOString(),
  };
}

export type WebhookEventType =
  | 'payment.verified'
  | 'payment.failed'
  | 'usage.recorded'
  | 'session.completed'
  | 'endpoint.created'
  | 'endpoint.updated'
  | 'endpoint.deleted';

export interface WebhookEvent {
  id: string;
  event: WebhookEventType;
  timestamp: string;
  data: Record<string, any>;
}

export interface WebhookConfig {
  id: string;
  endpointId?: string; // Optional: webhook for specific endpoint
  url: string;
  secret: string; // For HMAC signing
  events: WebhookEventType[];
  enabled: boolean;
  createdAt: string;
  updatedAt: string;
}

export interface WebhookDelivery {
  id: string;
  webhookConfigId: string;
  eventId: string;
  url: string;
  payload: string;
  signature: string;
  status: 'pending' | 'delivered' | 'failed';
  attempts: number;
  maxAttempts: number;
  nextRetryAt?: string;
  lastAttemptAt?: string;
  lastResponseCode?: number;
  lastResponseBody?: string;
  deliveredAt?: string;
  createdAt: string;
}

/**
 * Generate HMAC signature for webhook payload
 */
export function generateWebhookSignature(
  payload: string,
  secret: string
): string {
  return crypto
    .createHmac('sha256', secret)
    .update(payload)
    .digest('hex');
}

/**
 * Verify webhook signature
 */
export function verifyWebhookSignature(
  payload: string,
  signature: string,
  secret: string
): boolean {
  const expectedSignature = generateWebhookSignature(payload, secret);
  return crypto.timingSafeEqual(
    Buffer.from(signature),
    Buffer.from(expectedSignature)
  );
}

/**
 * Create webhook event
 */
export function createWebhookEvent(
  event: WebhookEventType,
  data: Record<string, any>
): WebhookEvent {
  return {
    id: `evt_${Date.now()}_${Math.random().toString(36).substring(2, 9)}`,
    event,
    timestamp: new Date().toISOString(),
    data,
  };
}

/**
 * Deliver webhook event with enhanced error handling and metrics
 */
export async function deliverWebhook(
  config: WebhookConfig,
  event: WebhookEvent,
  attempt: number = 1
): Promise<{ 
  success: boolean; 
  statusCode?: number; 
  error?: string;
  retryable?: boolean;
  retryDelay?: number;
}> {
  const payload = JSON.stringify(event);
  const signature = generateWebhookSignature(payload, config.secret);
  const isRetry = attempt > 1;
  
  const startTime = Date.now();
  
  try {
    const response = await fetch(config.url, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'X-Webhook-Signature': signature,
        'X-Webhook-Event': event.event,
        'X-Webhook-Timestamp': event.timestamp,
        'X-Webhook-Delivery-Id': event.id,
        'X-Webhook-Attempt': attempt.toString(),
        'User-Agent': 'NexFlow-Webhooks/1.0',
      },
      body: payload,
      signal: AbortSignal.timeout(WEBHOOK_CONFIG.requestTimeout),
    });
    
    const responseBody = await response.text().catch(() => '');
    const latencyMs = Date.now() - startTime;
    
    if (response.ok) {
      recordDeliveryMetric(config.url, true, isRetry);
      
      logger.info({
        webhookConfigId: config.id,
        eventId: event.id,
        eventType: event.event,
        statusCode: response.status,
        latencyMs,
        attempt,
      }, 'Webhook delivered successfully');
      
      return {
        success: true,
        statusCode: response.status,
      };
    } else {
      const retryable = isRetryableStatus(response.status);
      const retryDelay = retryable ? calculateRetryDelay(attempt - 1) : undefined;
      
      recordDeliveryMetric(config.url, false, isRetry);
      
      logger.warn({
        webhookConfigId: config.id,
        eventId: event.id,
        eventType: event.event,
        statusCode: response.status,
        responseBody: responseBody.substring(0, 200),
        latencyMs,
        attempt,
        retryable,
        retryDelay,
      }, 'Webhook delivery failed');
      
      return {
        success: false,
        statusCode: response.status,
        error: `HTTP ${response.status}: ${responseBody.substring(0, 100)}`,
        retryable,
        retryDelay,
      };
    }
  } catch (error) {
    const errorMessage = error instanceof Error ? error.message : 'Unknown error';
    const retryable = error instanceof Error && isRetryableError(error);
    const retryDelay = retryable ? calculateRetryDelay(attempt - 1) : undefined;
    const latencyMs = Date.now() - startTime;
    
    recordDeliveryMetric(config.url, false, isRetry);
    
    logger.error({
      webhookConfigId: config.id,
      eventId: event.id,
      eventType: event.event,
      error: errorMessage,
      latencyMs,
      attempt,
      retryable,
      retryDelay,
    }, 'Webhook delivery error');
    
    return {
      success: false,
      error: errorMessage,
      retryable,
      retryDelay,
    };
  }
}

/**
 * Calculate next retry delay with exponential backoff and jitter
 * 
 * Formula: delay = min(maxDelay, baseDelay * 2^attempt) * (1 + random * jitter)
 * 
 * @param attempt - Current attempt number (0-indexed)
 * @returns Delay in milliseconds
 */
function calculateRetryDelay(attempt: number): number {
  const { baseDelay, maxDelay, jitterFactor } = WEBHOOK_CONFIG;
  
  // Exponential backoff
  const exponentialDelay = baseDelay * Math.pow(2, attempt);
  const cappedDelay = Math.min(exponentialDelay, maxDelay);
  
  // Add jitter (random variance to prevent thundering herd)
  const jitter = 1 + (Math.random() * jitterFactor * 2 - jitterFactor);
  
  return Math.round(cappedDelay * jitter);
}

/**
 * Determine if an HTTP status code is retryable
 */
function isRetryableStatus(statusCode: number): boolean {
  // Retry on server errors (5xx) and some client errors
  if (statusCode >= 500 && statusCode < 600) return true;
  if (statusCode === 408) return true; // Request Timeout
  if (statusCode === 429) return true; // Too Many Requests
  return false;
}

/**
 * Determine if an error is retryable
 */
function isRetryableError(error: Error): boolean {
  const message = error.message.toLowerCase();
  return (
    message.includes('timeout') ||
    message.includes('econnrefused') ||
    message.includes('econnreset') ||
    message.includes('enotfound') ||
    message.includes('socket hang up') ||
    message.includes('network')
  );
}

/**
 * Queue webhook for delivery with retry logic (uses BullMQ)
 */
export async function queueWebhookDelivery(
  config: WebhookConfig,
  event: WebhookEvent
): Promise<void> {
  try {
    const payload = JSON.stringify(event);
    const signature = generateWebhookSignature(payload, config.secret);
    
    // Create delivery record in database
    await webhookDeliveriesDb.createDeliveryRecord({
      webhookConfigId: config.id,
      eventId: event.id,
      eventType: event.event,
      url: config.url,
      payload,
      signature,
      maxAttempts: WEBHOOK_CONFIG.maxAttempts,
    });
    
    // Enqueue job in BullMQ (lazy import)
    const enqueueWebhookDelivery = await getWebhookQueue();
    if (enqueueWebhookDelivery) {
      await enqueueWebhookDelivery({
        webhookConfigId: config.id,
        eventId: event.id,
        eventType: event.event,
        url: config.url,
        payload,
        signature,
      });
      
      logger.info({
        webhookConfigId: config.id,
        eventId: event.id,
        eventType: event.event,
        url: config.url,
        maxAttempts: WEBHOOK_CONFIG.maxAttempts,
      }, 'Webhook queued for delivery');
    } else {
      // Fallback: deliver with retry logic inline
      logger.warn({
        webhookConfigId: config.id,
        eventId: event.id,
      }, 'Webhook queue not available, delivering with inline retry');
      
      await deliverWithRetry(config, event);
    }
  } catch (error) {
    logger.error({
      webhookConfigId: config.id,
      eventId: event.id,
      error,
    }, 'Failed to queue webhook delivery');
    throw error;
  }
}

/**
 * Deliver webhook with retry logic (inline, without queue)
 */
async function deliverWithRetry(
  config: WebhookConfig,
  event: WebhookEvent
): Promise<void> {
  let attempt = 1;
  
  while (attempt <= WEBHOOK_CONFIG.maxAttempts) {
    const result = await deliverWebhook(config, event, attempt);
    
    if (result.success) {
      return;
    }
    
    // Check if we should retry
    if (!result.retryable || attempt >= WEBHOOK_CONFIG.maxAttempts) {
      // No retry - check if should go to DLQ
      if (WEBHOOK_CONFIG.enableDLQ) {
        await sendToDeadLetterQueue(config, event, result.error || 'Unknown error', attempt);
      }
      
      logger.error({
        webhookConfigId: config.id,
        eventId: event.id,
        eventType: event.event,
        attempts: attempt,
        error: result.error,
        sentToDLQ: WEBHOOK_CONFIG.enableDLQ,
      }, 'Webhook delivery failed permanently');
      
      return;
    }
    
    // Wait before retry
    const delay = result.retryDelay || calculateRetryDelay(attempt - 1);
    logger.info({
      webhookConfigId: config.id,
      eventId: event.id,
      attempt,
      nextAttempt: attempt + 1,
      delayMs: delay,
    }, 'Scheduling webhook retry');
    
    await new Promise(resolve => setTimeout(resolve, delay));
    attempt++;
  }
}

/**
 * Send failed webhook to dead letter queue for manual review
 */
async function sendToDeadLetterQueue(
  config: WebhookConfig,
  event: WebhookEvent,
  error: string,
  attempts: number
): Promise<void> {
  metrics.dlqCount++;
  
  try {
    await webhookDeliveriesDb.markDeliveryFailed({
      eventId: event.id,
      webhookConfigId: config.id,
      error,
      attempts,
      sentToDLQ: true,
    });
    
    logger.warn({
      webhookConfigId: config.id,
      eventId: event.id,
      eventType: event.event,
      error,
      attempts,
    }, 'Webhook sent to dead letter queue');
  } catch (dlqError) {
    logger.error({
      webhookConfigId: config.id,
      eventId: event.id,
      dlqError,
    }, 'Failed to send webhook to dead letter queue');
  }
}

/**
 * Attempt webhook delivery with retry logic (used by BullMQ worker)
 */
async function attemptWebhookDelivery(
  config: WebhookConfig,
  event: WebhookEvent,
  delivery: Omit<WebhookDelivery, 'id' | 'createdAt'>
): Promise<void> {
  delivery.attempts++;
  const result = await deliverWebhook(config, event, delivery.attempts);
  
  delivery.lastAttemptAt = new Date().toISOString();
  delivery.lastResponseCode = result.statusCode;
  
  if (result.success) {
    delivery.status = 'delivered';
    delivery.deliveredAt = new Date().toISOString();
    
    logger.info({
      webhookConfigId: config.id,
      eventId: event.id,
      attempts: delivery.attempts,
    }, 'Webhook delivered');
    
    return;
  }
  
  // Check if we should retry
  const canRetry = result.retryable && delivery.attempts < delivery.maxAttempts;
  
  if (!canRetry) {
    delivery.status = 'failed';
    
    // Send to DLQ if enabled
    if (WEBHOOK_CONFIG.enableDLQ) {
      await sendToDeadLetterQueue(config, event, result.error || 'Unknown error', delivery.attempts);
    }
    
    logger.error({
      webhookConfigId: config.id,
      eventId: event.id,
      attempts: delivery.attempts,
      maxAttempts: delivery.maxAttempts,
      error: result.error,
      retryable: result.retryable,
      sentToDLQ: WEBHOOK_CONFIG.enableDLQ,
    }, 'Webhook delivery failed after max attempts');
    
    return;
  }
  
  // Schedule retry
  const retryDelay = result.retryDelay || calculateRetryDelay(delivery.attempts - 1);
  delivery.nextRetryAt = new Date(Date.now() + retryDelay).toISOString();
  delivery.status = 'pending';
  
  logger.info({
    webhookConfigId: config.id,
    eventId: event.id,
    attempts: delivery.attempts,
    maxAttempts: delivery.maxAttempts,
    nextRetryAt: delivery.nextRetryAt,
    retryDelayMs: retryDelay,
  }, 'Webhook scheduled for retry');
  
  // Schedule retry (in production, BullMQ handles this)
  setTimeout(async () => {
    await attemptWebhookDelivery(config, event, delivery);
  }, retryDelay);
}

/**
 * Get webhook configs for an event type
 */
export async function getWebhookConfigsForEvent(
  eventType: WebhookEventType,
  endpointId?: string
): Promise<WebhookConfig[]> {
  try {
    const { getWebhookConfigs } = await import('./webhook-config-store');
    const allConfigs = await getWebhookConfigs({
      endpointId: endpointId || undefined,
      enabled: true,
    });
    
    return allConfigs.filter(config => {
      if (!config.enabled) return false;
      if (!config.events.includes(eventType)) return false;
      if (endpointId && config.endpointId && config.endpointId !== endpointId) return false;
      return true;
    });
  } catch (error) {
    logger.error('Failed to get webhook configs', { eventType, endpointId, error });
    return [];
  }
}

/**
 * Trigger webhook for an event
 */
export async function triggerWebhook(
  eventType: WebhookEventType,
  data: Record<string, any>,
  endpointId?: string
): Promise<void> {
  try {
    const event = createWebhookEvent(eventType, data);
    const configs = await getWebhookConfigsForEvent(eventType, endpointId);
    
    // Filter enabled configs
    const enabledConfigs = configs.filter(c => c.enabled);
    
    // Filter by endpoint if specified
    const relevantConfigs = endpointId
      ? enabledConfigs.filter(c => !c.endpointId || c.endpointId === endpointId)
      : enabledConfigs.filter(c => !c.endpointId);
    
    // Queue delivery for each config
    for (const config of relevantConfigs) {
      await queueWebhookDelivery(config, event);
    }
    
    logger.info('Webhooks triggered', {
      eventType,
      endpointId,
      configCount: relevantConfigs.length,
    });
  } catch (error) {
    logger.error('Failed to trigger webhook', {
      eventType,
      endpointId,
      error,
    });
  }
}

